
.title {
    color: aliceblue;
    font-family: fantasy;
    text-align: center;
}

.container {
    background-color: black;
    height: 100%;
    display: flex;
    justify-content: center;
  }
  
  .loader {
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 300;
  }
  
  .movie {
    position: relative;
    background-color: white;
    margin: 100px 100px;
    font-weight: 200;
    width: 500px;
    padding: 20px;
    border-radius: 5px;
    color: #adaeb9;
    display: grid;
    grid-template-columns: minmax(150px, 1fr) 2fr;
    grid-gap: 20px;
    text-decoration: none;
    color: inherit;
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
      0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
  }

  .movie__img {
    justify-content: right;
    top: 0px;
    max-width: 400px;
    width: 100%;
    margin-left: 300px;
    box-shadow: 0 30px 60px -12px rgba(50, 50, 93, 0.25),
      0 18px 36px -18px rgba(0, 0, 0, 0.3), 0 -12px 36px -8px rgba(0, 0, 0, 0.025);
  }
  
  .movie__title,
  .movie__year {
    margin: 0;
    font-weight: 200;
    font-family: fantasy;
    text-decoration: none;
    position: absolute;
  }
  
  .movie__title a {
    margin-bottom: 5px;
    font-size: 50px;
    color: #2c2c2c;
    text-decoration: none;
  }
  
  
  @media screen and (max-width: 1090px) {
    .movies {
      grid-template-columns: 1fr;
      width: 100%;
    }
  }
